import http from './http'
// 导入md5包，实现加密
import md5 from 'js-md5'



// ======注册接口
// 发送注册请求参数，并导出
export function register(name, psw) {
    // 返回一个Promise
    return http.post('/user/register', {
        loginName: name,
        password: psw
    })
}


// =======登录接口
// 发送登录请求向服务器传递参数，并导出
export function login(name, psw) {
    // 返回一个Promise
    return http.post("/user/login", {
        loginName: name,
        passwordMd5: md5(psw)
    })
}


// ======首页面数据接口
// 发送首页请求
export function getIndexInfo() {
    return http.get('/index-infos')
}


// ========详情页商品数据接口
export function goods(id) {
    return http.get(`/goods/detail/${id}`)
}


// ===========添加商品到购物车
export function addCart(options) {
    return http.post('/shop-cart', options)

}

// =======获取购物车列表
export function getCartList() {
    return http.get('/shop-cart')
}

// =========点击增加按钮时触发
export function plus(cartItemId, goodsCount) {
    return http.put('/shop-cart', {
        cartItemId,
        goodsCount,
    })
}


// =======删除商品时触发
export function deleteHeandle(cartItemId){
    return http.delete('/shop-cart/'+cartItemId)
}

// =========获取地址列表
export  function getAddressList(){
    return http.get('/address')
}

// ========根据ID获取单个地址实现回显
export function getAddressDetail(id){
    // console.log(id);
    return http.get('/address/'+id)
}

// =========修改地址
export function updateAddress(obj){
    return http.put('/address',obj)
}

// =========添加地址
export function addAddress(obj){
    return http.post('/address',obj)
}
//=========删除地址
export function deleteAddress(id){
    return http.delete('/address/'+id)
}

// =========获取订单列表
export function getOrderList(page,state){
    return http.get('/order',{
        params:{
            pageNumber:page,
            status:state,
        }
    })
}

// ======点击结算
export function cartSettle(cartItemId){
    // console.log(cartItemId);
    return http.get('shop-cart/settle?cartItemIds=' + cartItemId)
}

// ========生成订单
export function saveOrder(addressId, cartItemIds){
    return http.post('saveOrder',{
        addressId,
        cartItemIds
    })
}

// =========去支付
export function orderPay(orderNo){
    return http.get('order/' + orderNo)
}

// =======支付宝支付
export function Alipay(orderNo, payType){
    return http.get('paySuccess?orderNo=' + orderNo + '&payType=' + payType)
}
// ========确认支付
export function confirmPayment(orderNo){
    return http.put('order/' + orderNo+'/finish')
}
// ========取消订单
export function cancelPayment(orderNo) {
    return http.put('order/' + orderNo + '/cancel')
}
// ========获取用户登录数据
export function loginInfo() {
    // 返回一个Promise
    return http.get("/user/info")
}
// =========保存修改用户信息
export function saveInfo(introduceSign, nickName, passwordMd5) {
    // 返回一个Promise
    return http.put("/user/info",{
        introduceSign,
        nickName, 
        passwordMd5: md5(passwordMd5)
    })
}
// =========退出登录
export function logoOut(){
    return http.post('/user/logout')
}
// ==========搜索页点击搜索
export function searchHandle(pageNumber, goodsCategoryId, keyword, orderBy){
    // console.log(pageNumber);
    return http.get('/search?pageNumber=' + pageNumber + '&goodsCategoryId=' + goodsCategoryId + '&keyword=' + keyword + '&orderBy=' + orderBy)
}
//============分类数据
export function shopClass(){
    return http.get('/categories')
}
