import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: () => import(/*webpackChunkName:"home"*/'../views/Home/Home'),
    meta:{
      isShowNav:true
    }
  },
  {
    path: '/categary',
    component: () => import(/*webpackChunkName:"categary"*/'../views/Categary/Categary'),
    meta:{
      isShowNav:true
    }
  },
  {
    path: '/cart',
    component: () => import(/*webpackChunkName:"cart"*/'../views/Cart/Cart'),
    meta:{
      isShowNav:true,
      // isShowNav:false,
      til:'购物车',
    }
  },
  {
    path: '/user',
    component: () => import(/*webpackChunkName:"user"*/'../views/User/User'),
    meta:{
      isShowNav:true
    }
  },
  {
    path: '/login',
    component: () => import(/*webpackChunkName:"login"*/'../views/Login/Login'),
    meta:{
      isShowNav:false,
      til:'登录',
    }
  },
  {
    path:"/search",
    component: () => import(/*webpackChunkName:"search"*/'../views/Search/Search'),
    meta:{
      isShowNav:false
    }
  },
  {
   path:'/info/:productId',
    component: () => import(/*webpackChunkName:""*/'../views/Info/info'),
   meta:{
    isShowNav:false
  }
  },
  {
    path:'/addressedit',
    component: () => import(/*webpackChunkName:"info"*/'../views/Address/AddressEdit'),
    meta:{
      isShowNav:false
    }
  },
  {
    path: '/addresslist',
    component: () => import(/*webpackChunkName:"addresslist"*/'../views/Address/AddressList'),
    meta: {
      isShowNav: false
    }
  },
  {
    path:'/create-order',
    component: () => import(/*webpackChunkName:"create-order"*/"../views/CreateOrder/CreateOrder"),
    meta:{
      isShowNav:false,
    }
  },
  {
    path:'/order',
    component: () => import(/*webpackChunkName:"order"*/'../views/Order/Order'),
    mete:{
      isShowNav:false,
      til:"订单页"
    }
  },
  {
    path: '/order-detail',
    component: () => import(/*webpackChunkName:"order-detail"*/'../views/orderDetail/orderDetail'),
    mete: {
      isShowNav: false,
      til: "订单详情"
    }
  },
  {
    path:'/account',
    component: () => import(/*webpackChunkName:"account"*/'../views/Account/Account'),
    mete: {
      isShowNav: false,
      til: "账号管理"
    }
  },
  {
    path:'/about',
    component: () => import(/*webpackChunkName:"about"*/'../views/aboutOur/aboutOur'),
    meta:{
      isShowNav:false,
    }
  }

]

const router = new VueRouter({
  routes
})

// 全局导航守卫
router.beforeEach((to,from,next)=>{
  document.title=to.meta.til || "新蜂商城"
  next();
})

export default router
