import axios from 'axios'
import router from "@/router/index"
// 导入vant组件库 消息通知组件
// import { Notify } from 'vant'
import { Toast } from 'vant';

// 创建axios实例
let http = axios.create({
    // 通过npm run serve运行，会在process.env上绑定一个NODE_ENV变量，NODE_ENV:"development"是开发环境
    // 通过npm run build运行，会在process.env上绑定一个NODE_ENV变量，NODE_ENV:"production"是生产环境
    // 在vue.config.js文件中创建代理服务器解决跨域问题 配置完代理重启服务器
    baseURL: process.env.NODE_ENV == 'production' ? 'http://backend-api-01.newbee.ltd/api/v1' : '/api/v1'
})


// 添加请求拦截器
http.interceptors.request.use(function (config) {
    // 每次请求带着token令牌
    config.headers.token=localStorage.getItem("xftoken")
    return config;
}), function (error) {
    // 对请求错误做些什么
    return Promise.reject(error)
}

// 添加响应拦截器
http.interceptors.response.use(function (response) {
    // 对响应数据做些什么
    let data = response.data;
    // 注册成功后，再注册时 服务器会响应
    // data.resultCode !== 200 已经注册
    if (data.resultCode != 200) {
            // 使用Notify的vant组件
        Toast.fail(data.message);
        //    Notify({type:'danger',message:data.message || "系统繁忙"})
    }
    if (data.resultCode === 416) {
        // 说明没有登录
        router.push('/login?needback=1')
    }
    return response.data
}), function (error) {
    // 对响应失败给出提示
    Notify({type:'danger',message:"系统繁忙"});
    return Promise.reject(error)
}


// 导出axios实例
export default http